* {
  margin: 0;
  padding: 0;
}

.container {
  display: flex;
  margin: 0;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.form {
  display: flex;
  height: 100%;
  width: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(#DAA520, #B8860B);
}

.form label {
  color: white;
  font-size: 20px;
  font-weight: light;
  font-family: Poppins;
}

.form input {
  color: #FFF;
  font-size: 20px;
  font-family: Poppins;
  background-color: transparent;
  border: 1px solid #FFF;
  border-radius: 8px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  padding: 20px;
  width: 160px;
}

.form input::placeholder {
  color: #FFF;
  font-size: 14px;
  opacity: 0.7;
  
}

.form button {
  color: #B8860B;
  font-size: 20px;
  font-family: Poppins;
  border: 30px;
  background-color: #FFF;
  border-radius: 8px;
  width: 100%;
  height: 40px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  cursor: pointer;
}

.containerButton {
  display: flex;
  flex-direction: column;
  width: 170px;
  height: 30%;
  justify-content: space-around;
}

.containerInputLabel {

  height: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.childenContainerInputLabel {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 15px;
}

